// React
import React, { useEffect, useState } from 'react'
import axios from 'axios'
// CSS
import './App.css'
// Icon Library
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// Service Worker
import { serviceWorkerRegistration } from './serviceWorkerRegistration'
import { getRegistration } from './serviceWorkerRegistration'
// Database
import { NewUpdatePost, UpdatePost } from './AppData/UpdateData.js'
import {
  MenuServiceData,
  MenuFastLinkData,
  HomePostData,
  HomeServiceData,
  PostData,
  ServiceData,
  CourseScheduleData,
  ToolsData,
  LeftTitle,
} from './AppData/AppData.js'

// 頁面架構組件
function App() {
  // 測試版功能訪問權
  const [betaVersion, setBetaVersion] = useState(false)
  const betaModeChange = () => {
    setBetaVersion((prevActive) => !prevActive)
  }

  // 自動檢查更新
  const [updateAvailable, setUpdateAvailable] = useState(false)
  useEffect(() => {
    const intervalId = setInterval(() => {
      if ('serviceWorker' in navigator) {
        navigator.serviceWorker
          .register('/classdata-app/service-worker.js')
          .then((registration) => {
            registration.update()
            console.log(
              'ServiceWorker 註冊成功！ 管轄範圍：',
              registration.scope
            )
            // 檢查是否有新版本可用
            if (registration.waiting) {
              setUpdateAvailable(true)
            }
            registration.addEventListener('updatefound', () => {
              const installingWorker = registration.installing
              if (installingWorker) {
                installingWorker.addEventListener('statechange', () => {
                  if (
                    installingWorker.state === 'installed' &&
                    navigator.serviceWorker.controller
                  ) {
                    // 提示用戶更新
                    setUpdateAvailable(true)
                  }
                })
              }
            })
            // 當新版本可用時觸發
            serviceWorkerRegistration.register({
              onUpdate: (reg) => {
                // 提示用戶更新
                setUpdateAvailable(true)
              },
            })
          })
          .catch((error) => {
            console.log('ServiceWorker 註冊失敗：', error)
          })
      }
    }, 5000)

    return () => clearInterval(intervalId)
  }, [])
  // 用戶手動更新新版本
  const handleUpdate = async () => {
    const registration = await getRegistration()
    if (registration && registration.waiting) {
      // 強制激活新版本
      registration.waiting.postMessage({ type: 'SKIP_WAITING' })
      setUpdateAvailable(false)
      window.location.reload()
    }
  }

  // 網頁加載狀態
  const [showLoader, setShowLoader] = useState(true)
  window.onload = () => {
    setShowLoader(false)
  }
  window.onunload = () => {
    setShowLoader(true)
  }

  // 設備狀態
  const [OperationalStatus, setOperationalStatus] = useState('No Status')

  // 頁面狀態
  const [homeSelect, setHomeSelect] = useState(true) // 首頁 (預設)
  const [postSelect, setPostSelect] = useState(false) // 公告
  const [folderSelect, setFolderSelect] = useState(false) // 服務
  const [developmentSelect, setDevelopmentSelect] = useState(false) // 開發
  const [courseScheduleSelect, setcourseScheduleSelect] = useState(false) // 課程表
  const [settingPage, setSettingPage] = useState(false) // 設定
  const [menuActive, setMenuActive] = useState(false) // 菜單欄狀態

  // 切換菜單按鈕狀態
  const menuBtnClick = () => {
    setMenuActive((prevActive) => !prevActive)
  }

  // 提示彈窗狀態
  const [TipsActive, setTipsActive] = useState(true)
  const Tips = () => {
    setTipsActive((prevActive) => !prevActive)
  }

  // 刷新
  function reload() {
    window.location.reload(true)
  }

  // 網址參數 (剛修復的元老級 Bug)
  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search)
    const page = urlParams.get('page')

    switch (page) {
      case 'home':
        setHomeSelect(true)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'post':
        setHomeSelect(false)
        setPostSelect(true)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'folder':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(true)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'development':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(true)
        setcourseScheduleSelect(false)
        break
      case 'courseSchedule':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(true)
        break
      default:
        // 若URL參數不是上述五種狀態，則將狀態全部設為false
        setHomeSelect(true)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
    }

    // 監聽 popstate 事件
    window.addEventListener('popstate', handlePopState)

    // 在useEffect的回傳函數中移除事件監聽器
    return () => {
      window.removeEventListener('popstate', handlePopState)
    }
  }, [])

  // 定義監聽事件的處理函數
  const handlePopState = () => {
    const urlParams = new URLSearchParams(window.location.search)
    const page = urlParams.get('page')

    switch (page) {
      case 'home':
        setHomeSelect(true)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'post':
        setHomeSelect(false)
        setPostSelect(true)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'folder':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(true)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'development':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(true)
        setcourseScheduleSelect(false)
        break
      case 'courseSchedule':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(true)
        break
      default:
        // 若URL參數不是上述五種狀態，則將狀態全部設為false
        setHomeSelect(true)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
    }
  }

  // 切換頁面跳轉並更改參數
  function pagelink(state) {
    // 變更網站狀態

    switch (state) {
      case 'home':
        setHomeSelect(true)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'post':
        setHomeSelect(false)
        setPostSelect(true)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'folder':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(true)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(false)
        break
      case 'development':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(true)
        setcourseScheduleSelect(false)
        break
      case 'courseSchedule':
        setHomeSelect(false)
        setPostSelect(false)
        setFolderSelect(false)
        setDevelopmentSelect(false)
        setcourseScheduleSelect(true)
        break
      default:
        break
    }

    setMenuActive(false)
    // 更新URL參數
    const newUrl = `?page=${state}`
    window.history.pushState({ page: state }, '', newUrl)
  }

  // 外觀模式切換
  const [theme, setTheme] = useState()
  const [themeMode, setThemeMode] = useState(() => {
    const savedThemeMode = localStorage.getItem('themeMode')
    return savedThemeMode !== null ? parseInt(savedThemeMode) : 1 // 預設為「根據系統」
  })
  const [modeValue, setModeValue] = useState()
  const [themeInfo, setThemeInfo] = useState('Error')

  // 監測系統狀態變化
  const prefersDarkMode = window.matchMedia('(prefers-color-scheme: dark)')

  // 變更狀態，並且在檢測到系統狀態變化後執行
  useEffect(() => {
    const prefersDarkMode = window.matchMedia('(prefers-color-scheme: dark)')
    if (themeMode === 1) {
      if (prefersDarkMode.matches) {
        setTheme('dark')
        setThemeInfo('Dark')
        setModeValue('根據系統')
      } else if (!prefersDarkMode.matches) {
        setTheme('')
        setThemeInfo('Light')
        setModeValue('根據系統')
      }
    } else if (themeMode === 2) {
      setTheme('dark')
      setThemeInfo('Dark')
      setModeValue('深色模式')
    } else if (themeMode === 0) {
      setTheme('')
      setModeValue('淺色模式')
    }
    prefersDarkMode.addEventListener('change', systemThemeChange)
    return () => {
      prefersDarkMode.removeEventListener('change', systemThemeChange)
    }
  }, [themeMode, prefersDarkMode])

  // 監測並使用 localStorage 儲存狀態
  useEffect(() => {
    localStorage.setItem('themeMode', themeMode.toString())
  }, [themeMode])

  // 狀態切換輪迴
  const handleThemeChange = (event) => {
    if (themeMode === 1) {
      setTheme('dark')
      setThemeMode(2)
      setModeValue('深色模式')
      setThemeInfo('Dark Mode')
    } else if (themeMode === 2) {
      setTheme('')
      setThemeMode(0)
      setModeValue('淺色模式')
      setThemeInfo('Light Mode')
    } else if (themeMode === 0) {
      setThemeMode(1)
      setModeValue('根據系統')
    }
  }

  //「根據系統」選項，檢測系統主題並切換狀態
  const systemThemeChange = () => {
    const prefersDarkMode = window.matchMedia('(prefers-color-scheme: dark)')
    if (themeMode === 1) {
      if (prefersDarkMode.matches) {
        setTheme('dark')
        setModeValue('根據系統')
      } else if (!prefersDarkMode.matches) {
        setTheme('')
        setModeValue('根據系統')
      }
    }
  }

  // 複製當前網址（尚未使用）
  function CopyUrlButton() {
    const handleClick = () => {
      navigator.clipboard.writeText(window.location.href)
    }
  }

  return (
    <>
      <nav
        className={`${theme}${theme && settingPage ? ' ' : ''}${
          settingPage ? 'settingOpen' : ''
        }`}>
        <div
          id="header"
          className={`${menuActive ? 'open' : ''}`}
          style={{
            boxShadow: menuActive ? '0 1px 1px 0px #f1f1f100' : '',
          }}>
          <ul id="header-ul">
            <li id="header-li" className="version">
              <span
                title={`當前版本：${NewUpdatePost[0].title}`}
                onClick={() => [pagelink('development')]}>
                {NewUpdatePost[0].title}
              </span>
            </li>
            <li id="header-li">
              <img
                className="icon"
                onClick={reload}
                src={`${process.env.PUBLIC_URL}/classdata-icon/classdata-icon.001.png`}
                alt="班級資訊平台icon"
                title="[回首頁] 班級資訊平台icon"></img>
            </li>
            <li
              id="header-li"
              className="menuicon"
              onClick={() => setSettingPage(true)}>
              <FontAwesomeIcon icon="fa-solid fa-gear" />
            </li>
          </ul>
        </div>

        <menu
          id="menu"
          className={`${theme}${theme && settingPage ? ' ' : ''}${
            settingPage ? 'settingOpen' : ''
          } ${menuActive ? 'open' : ''}`}>
          <div id="menu-view" className={`${menuActive ? 'show' : ''}`}>
            <div id="menu-block" className="menu-block">
              <span className={`${menuActive ? '' : 'opacity-0'}`}>
                核心服務
              </span>
              <ul id="menu-linkView">
                {MenuServiceData.map((MenuServiceData) => (
                  <li
                    className={`${MenuServiceData.class} ${
                      menuActive ? '' : 'close'
                    }`}
                    style={{ backgroundImage: MenuServiceData.style }}
                    onClick={() => pagelink(MenuServiceData.link)}>
                    <div
                      className="space"
                      style={{ height: MenuServiceData.divHight }}></div>
                    <h1>{MenuServiceData.name}</h1>
                  </li>
                ))}
              </ul>
            </div>
            <div id="menu-block" className="menu-block">
              <span className={`${menuActive ? '' : 'opacity-0'}`}>捷徑</span>
              <ul id="menu-linkView">
                {MenuFastLinkData.map((MenuFastLinkData) => (
                  <li
                    className={`${MenuFastLinkData.class} ${
                      menuActive ? '' : 'close'
                    }`}
                    style={{ backgroundImage: MenuFastLinkData.style }}
                    onClick={MenuFastLinkData.link}>
                    <img
                      src={MenuFastLinkData.icon}
                      alt={MenuFastLinkData.name}></img>
                    <h1>{MenuFastLinkData.name}</h1>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </menu>
      </nav>
      <footer
        className={`${theme}${theme && settingPage ? ' ' : ''}${
          settingPage ? 'settingOpen' : ''
        }`}
        style={{
          boxShadow: menuActive ? '0 -1px 1px 0px #f1f1f100' : '',
        }}>
        <div id="footer">
          <ul id="footer-ul">
            <li id="footer-li" className="" onClick={() => [pagelink('home')]}>
              <div className={`li-view home ${homeSelect ? 'active' : ''}`}>
                <i className="fa-solid fa-house"></i>
                <span>首頁</span>
              </div>
            </li>
            <li id="footer-li" className="" onClick={() => [pagelink('post')]}>
              <div className={`li-view post ${postSelect ? 'active' : ''}`}>
                <i className="fa-solid fa-bell"></i>
                <span>公告</span>
              </div>
            </li>
            <li
              id="footer-li"
              className=""
              onClick={() => [pagelink('folder')]}>
              <div
                className={`li-view folder ${
                  folderSelect || courseScheduleSelect ? 'active' : ''
                }`}>
                <i className="fa-solid fa-shapes"></i>
                <span>服務</span>
              </div>
            </li>
            <li
              id="footer-li"
              className=""
              onClick={() => [pagelink('development')]}>
              <div
                className={`li-view development ${
                  developmentSelect ? 'active' : ''
                }`}>
                <i className="fa-solid fa-code"></i>
                <span>開發日誌</span>
              </div>
            </li>
            <li id="footer-li" className="menuicon" onClick={menuBtnClick}>
              <div className={`li-view menuicon ${menuActive ? 'active' : ''}`}>
                <FontAwesomeIcon icon="fa-solid fa-bars" />
              </div>
            </li>
          </ul>
        </div>
      </footer>
      {homeSelect && (
        <Home pagelink={pagelink} theme={theme} settingPage={settingPage} />
      )}
      {postSelect && <Post theme={theme} settingPage={settingPage} />}
      {folderSelect && (
        <Service pagelink={pagelink} theme={theme} settingPage={settingPage} />
      )}
      {developmentSelect && (
        <Development
          OperationalStatus={OperationalStatus}
          theme={theme}
          themeInfo={themeInfo}
          settingPage={settingPage}
        />
      )}
      {courseScheduleSelect && (
        <CourseSchedule
          TipsActive={TipsActive}
          Tips={Tips}
          theme={theme}
          settingPage={settingPage}
        />
      )}
      <Installation setOperationalStatus={setOperationalStatus} />
      {updateAvailable && (
        <div id="update">
          <div className="updateContent">
            <p>{NewUpdatePost[0].updateContent}</p>
          </div>
          <div className="updateBtn" onClick={handleUpdate}>
            <div>更新</div>
          </div>
        </div>
      )}
      <Setting
        settingPage={settingPage}
        setSettingPage={setSettingPage}
        menuActive={menuActive}
        handleThemeChange={handleThemeChange}
        modeValue={modeValue}
        theme={theme}
        betaModeChange={betaModeChange}
        betaVersion={betaVersion}
        setBetaVersion={setBetaVersion}></Setting>
      <Chat betaVersion={betaVersion} />
      {/* <div
        id="loader"
        className={`${theme} ${showLoader ? 'loading' : ''}`}></div> */}
    </>
  )
}

// 首頁組件
