// 開發頁面組件
function Development(props) {
  // 資料處理 \n 自動換行
  const formatContent = (content) => {
    return content
      .split('\n')
      .map((line, index) => <span key={index}>{line}</span>)
  }

  // 收合指定數量外的更新日誌
  const [showAll, setShowAll] = useState(false)
  const visiblePosts = showAll ? UpdatePost : UpdatePost.slice(0, 1)

  const handleToggleShowAll = () => {
    setShowAll(!showAll)
  }

  return (
    <main
      id="development"
      className={`${props.theme}${props.theme && props.settingPage ? ' ' : ''}${
        props.settingPage ? 'settingOpen' : ''
      }`}>
      <div id="development-view">
        <div id="development-view-content">
          <h1>當前版本</h1>
          <section className="development-block">
            {NewUpdatePost.map((NewUpdatePost) => (
              <div className="development-post bg-purple-blue">
                <h1>{NewUpdatePost.title}</h1>
                <h5>{NewUpdatePost.time}</h5>
                {formatContent(NewUpdatePost.content)}
              </div>
            ))}
          </section>
          <h1>歷史版本</h1>
          {/* <section className="development-block">
            {UpdatePost.map(UpdatePost => (
              <div className="development-post bg-history">
                <h3>{UpdatePost.title}</h3>
                <h5>{UpdatePost.time}</h5>
                {formatContent(UpdatePost.content)}
              </div>
            ))}
          </section> */}
          {UpdatePost.length > 1 && (
            <button
              className={`expand-button ${showAll ? 'open' : ''}`}
              onClick={handleToggleShowAll}>
              <FontAwesomeIcon
                icon={
                  showAll
                    ? 'fa-solid fa-circle-chevron-up'
                    : 'fa-solid fa-circle-chevron-down'
                }
              />
              {showAll ? '　收合　' : '展開全部'}
            </button>
          )}
          <section className="development-block">
            {visiblePosts.map((post, index) => (
              <div
                key={index}
                className="development-post bg-history"
                style={{ scrollSnapAlign: showAll ? 'start' : '' }}>
                <h3>{post.title}</h3>
                <h5>{post.time}</h5>
                {formatContent(post.content)}
              </div>
            ))}
            <div className="moreVersion" onClick={handleToggleShowAll}>
              {!showAll && (
                <>
                  <span>更多版本</span>
                  <FontAwesomeIcon icon="fa-solid fa-caret-down" />
                </>
              )}
              {showAll && (
                <>
                  <FontAwesomeIcon icon="fa-solid fa-sort-up" />
                  <span>收起</span>
                </>
              )}
            </div>
          </section>

          <h1>開發資訊</h1>
          <section className="development-block">
            <div className="development-post bg-em">
              <h5>User-Agent</h5>
              <span>{navigator.userAgent}</span>
            </div>
            <div className="development-post bg-em">
              <h5>Equipment Type</h5>
              <span>{props.OperationalStatus}</span>
            </div>
            <div className="development-post bg-em">
              <h5>Theme Mode</h5>
              <span>{props.themeInfo}</span>
            </div>
          </section>
          <hr style={{ color: '#000000' }} />
          <section className="development-block">
            <div>
              <div>
                <FontAwesomeIcon icon="fa-brands fa-github" />
                　敬請期待...
              </div>
            </div>
          </section>
        </div>
      </div>
    </main>
  )
}

// 課程表頁面組件
function CourseSchedule(props) {
  // 課程表樣式判斷
  function courseScheduleMerger(number) {
    if (typeof number !== 'string' || !number) {
      return ''
    }

    if (number.includes('classchange')) {
      if (number.includes('2')) {
        return 'Double classchange'
      } else if (number.includes('3')) {
        return 'Triple classchange'
      } else if (number.includes('AllDay')) {
        return 'AllDay classchange'
      } else {
        return 'classchange'
      }
    } else if (number.includes('AllDay') && !number.includes('classchange')) {
      if (number.includes('Vacances')) {
        return 'AllDay Vacances'
      } else {
        return 'AllDay'
      }
    } else {
      if (number.includes('2')) {
        return 'Double'
      } else if (number.includes('3')) {
        return 'Triple'
      } else if (number.includes('AllDay')) {
        return 'AllDay'
      }
    }

    return ''
  }

  // 課程表狀態
  const [courseScheduleDataInfoActive, setcourseScheduleDataInfoActive] =
    useState(false)
  const courseScheduleDataInfo = () => {
    setcourseScheduleDataInfoActive((prevActive) => !prevActive)
  }

  return (
    <main
      id="courseSchedule"
      className={`${props.theme}${props.theme && props.settingPage ? ' ' : ''}${
        props.settingPage ? 'settingOpen' : ''
      }`}>
      <div
        id="ctrlCourseScheduleInfo"
        className={`${courseScheduleDataInfoActive ? 'open' : ''}`}
        onClick={courseScheduleDataInfo}>
        <i class="fa-solid fa-circle-info"></i>
      </div>
      {CourseScheduleData.map((course, index) => (
        <section
          id="CSView"
          style={{ maxWidth: courseScheduleDataInfoActive ? '676px' : '' }}
          key={index}>
          <h1>{course.type}</h1>
          <div className="leftMask"></div>
          <div id="TableDiv">
            <div className="Table_Rows">
              <div className="Table_TopTitle">　</div>
              {LeftTitle.map((LeftTitle, k) => (
                <div
                  className={`Table_LeftTitle ${
                    courseScheduleDataInfoActive ? 'open' : ''
                  }`}
                  key={k}>
                  {LeftTitle.LeftTitle}
                </div>
              ))}
            </div>
            {course.data.map((data, i) => (
              <div className="Table_Rows" key={i}>
                <div className="Table_TopTitle">{data.Date}</div>
                {data.Course.map((Course, j) => (
                  <>
                    <div
                      className={`Table_Data ${
                        courseScheduleDataInfoActive ? 'open' : ''
                      } ${courseScheduleMerger(Course.continuity)}`}>
                      {Course.class}
                      <span
                        className={`${
                          courseScheduleDataInfoActive ? 'open' : ''
                        }`}>
                        {Course.info_teacher}
                      </span>
                      <span
                        className={`${
                          courseScheduleDataInfoActive ? 'open' : ''
                        }`}>
                        {Course.info_classroom}
                      </span>
                    </div>
                  </>
                ))}
              </div>
            ))}
          </div>
          <div className="rightMask"></div>
        </section>
      ))}
      <div id="Tips" style={{ display: props.TipsActive ? 'flex' : 'none' }}>
        <div>
          <span>
            點擊 <i class="fa-solid fa-circle-info"></i> 可展開/收合課程詳細資訊
          </span>
        </div>
        <div id="closeBtnView" onClick={props.Tips}>
          <div id="closeBtn">
            <div className="BtnLineL"></div>
            <div className="BtnLineR"></div>
          </div>
        </div>
      </div>
    </main>
  )
}

